.projectsBlock {
    padding: 50px 0 50px 0;
    min-height: 100vh;
    background-color: #020312;
}
.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}
@media screen and (max-width: 1003px) {
    .projects {
        justify-content: center;
    }
}